<script>
/**
 * @desc fullscreen of monitoring screen map
 * @auth Demon3443002624@qq.com
 * @date 2022-7-28 13:55:28
 */

import EchartsMapRotation from './EchartsMapRotation/index.vue'
import EchartsMapRotationCountry from './EchartsMapRotation/components/Country.vue'

export default {
  name: 'EchartsMapRotationFs',
  components: {
    EchartsMapRotation,
    EchartsMapRotationCountry
  },
  data() {
    return {
      mapType: 'rotation',
      mapData: {},
      refreshNum: 0
    }
  },
  props: [''],
  //   inject: [''],
  //   provide() {
  //     return {}
  //   },
  computed: {},
  watch: {},
  methods: {
    handleMapBackBtn() {
      this.mapType = 'rotation'
    },
    handleMapRefreshBtn() {
      this.refreshNum = Math.random()
    },
    handleToNation() {
      this.mapType = 'rotation-country'
    }
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {
    const query = this.$route.query
    const type = query.type
    type === 'country' ? this.handleToNation() : ''
    console.log('this.$route', this.$router, type)
  },
  beforeUpdated() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {}
}
</script>
<template>
  <div id="ms-map-fullscreen">
    <!-- <h1>fullscreen(in development...) query:{{ this.$route.query.type }}</h1> -->
    <div class="map">
      <el-row class="map-title">
        {{ mapType === 'rotation' ? '智能装备全球辐射图' : '智能装备中国分布图' }}
        <div class="control-btn-container">
          <svg-icon
            v-show="mapType !== 'rotation'"
            slot="prefix"
            title="点击返回智能装备全球辐射图"
            @click="handleMapBackBtn"
            icon-class="fpss-back-white"
          />
          <svg-icon
            slot="prefix"
            title="点击恢复初始状态"
            @click="handleMapRefreshBtn"
            icon-class="fpss-navigation"
          />
        </div>
      </el-row>
      <echarts-map-rotation
        v-if="mapType == 'rotation'"
        :mapdata="mapData"
        :refreshFlag="refreshNum"
        @toNation="handleToNation"
      />
      <echarts-map-rotation-country
        v-if="mapType == 'rotation-country'"
        :mapdata="mapData"
        :refreshFlag="refreshNum"
        @toNation="handleToNation"
        @handleMapBackBtn="handleMapBackBtn"
      />
    </div>
  </div>
</template>

<style lang='less' scoped>
#ms-map-fullscreen {
  height: 100%;
  color: #fff;
  background: #112241;
  .map {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    &-title {
      margin: 15px 15px;
      font-size: 20px;
      padding-left: 8px;
      font-weight: bold;
      line-height: 20px;
      border-left: 4px solid #20fb8e;
      .control-btn-container {
        top: 0;
        right: 0;
        cursor: pointer;
        font-size: 1.7rem;
        position: absolute;
      }
    }
  }
}
</style>


