<script>
/**
 * @desc map component
 * @auth Demon3443002624@qq.com
 * @date 2022-7-14 15:53:26
 * @desc refer to https://www.isqqw.com/echartsdetail?id=15176
 * @desc prototype design https://vo4xwk.axshare.com/#id=ihp963&p=%E8%BF%90%E7%BB%B4%E5%A4%A7%E5%B1%8F&g=1
 */

import { world } from './register'
import Ranking from './components/Ranking.vue'

export default {
  name: 'EchartsMapRotation',
  components: {
    Ranking
  },
  data() {
    return {
      zoom: 1.25,
      mapData: [],
      chart: null,
      center: [6, 17],
      // autoplay data
      autoplayFlag: true,
      autoplayTimer: null,
      autoplayTime: 10,
      autoplayTimeMax: 10,
      tooltipWater: null,
      // interval of autoplay,ms
      autoplayDelay: 1000,
      dialogVisible: false,
      // whether show ranking(static)
      rankingVisible: true,
      chartDataOriginal: [],
      rentingStateLabelMapping: {
        GensetInfoStatus_0Count: '待租赁',
        GensetInfoStatus_1Count: '租赁中',
        GensetInfoStatus_2Count: '维修',
        GensetInfoStatus_3Count: '施工',
        GensetInfoStatus_4Count: '检修'
      },
      operationStateLabelMapping: {
        GensetStatusAlarm: '故障',
        GensetStatusOffline: '离线',
        GensetStatusRun: '正常',
        GensetStatusStop: '停机',
        GensetStatusWarn: '风险'
      },
      // center of every country(static)
      centerOfCountry: {
        中国: [116.412318, 39.906965],
        沙特阿拉伯: [46.42, 24.41]
      }
    }
  },
  props: ['refreshFlag'],
  computed: {},
  watch: {
    tooltipWater(nv) {
      const btn = document.getElementById('ms-to-nation-btn')
      btn.addEventListener('click', this.handleToNation)
    },
    refreshFlag() {
      this.chart.setOption({
        geo: {
          zoom: this.zoom,
          center: this.center
        }
      })
    },
    autoplayTime(nv) {
      nv === 0 && this.handleToNation()
    }
  },
  methods: {
    // get map data(country&province)
    getMapData() {
      this.$api.monitoringScreenOperation
        .findGensetProjectIndexMapCountryProvince()
        .then((value) => {
          // value.Data.push({
          //   GensetCount: 1500,
          //   GensetInfoStatus_0Count: 20,
          //   GensetInfoStatus_1Count: 21,
          //   GensetInfoStatus_2Count: 1,
          //   GensetInfoStatus_3Count: 3,
          //   GensetInfoStatus_4Count: 0,
          //   GensetStatusAlarm: 1,
          //   GensetStatusOffline: 36,
          //   GensetStatusRun: 2,
          //   GensetStatusStop: 3,
          //   GensetStatusWarn: 3,
          //   Name: '沙特阿拉伯'
          // })
          // value.Data.shift()
          const d = value.Data
          const chartDataOriginal = new Array()
          const cities = new Array()
          const moveLines = new Array()
          if (!!d && Array.isArray(d)) {
            d.forEach((e) => {
              if (Number(e.GensetCount) > 0) {
                const obj = {
                  name: e.Name,
                  num: e.GensetCount
                }
                chartDataOriginal.push(obj)
                cities.push({
                  name: e.Name,
                  value: [
                    ...this.centerOfCountry[e.Name],
                    {
                      ...e
                    }
                  ]
                })
                e.Name != '中国' &&
                  moveLines.push({
                    fromName: '中国',
                    toName: e.Name,
                    coords: [this.centerOfCountry['中国'], this.centerOfCountry[e.Name]]
                  })
              }
            })
            if (chartDataOriginal.length < 10) {
              for (let i = chartDataOriginal.length - 1; i < 10; i++) {
                chartDataOriginal.push({
                  name: '',
                  num: ''
                })
              }
            }
            this.mapData = d
            this.chartDataOriginal = chartDataOriginal
            try {
              this.chart ||
                (this.chart = this.$echarts.init(
                  document.querySelector('.e-charts-map-rotation'),
                  null,
                  {
                    // renderer: 'svg'
                  }
                ))
            } catch (err) {}
            this.$echarts.registerMap('world', world)
            const option = {
              backgroundColor: 'rgb(16,34,66)',
              // global
              tooltip: {
                confine: true,
                hideDelay: 0.5,
                trigger: 'item',
                enterable: true,
                transitionDuration: 0.5,
                position: (point) => {
                  return [point[0], point[1]]
                },
                backgroundColor: 'rgba(0,0,0,0.5)'
              },
              grid: {
                right: '8%'
              },
              geo: {
                map: 'world',
                show: true,
                aspectScale: 0.65,
                zoom: this.zoom,
                center: this.center,
                scaleLimit: {
                  min: 1,
                  max: 5
                },
                label: {
                  show: false,
                  color: 'transparent'
                },
                // geo base chart
                tooltip: {
                  show: true,
                  formatter: (_) => ''
                },
                emphasis: {
                  label: {
                    color: 'rgba(255, 255, 255)'
                  }
                },
                roam: true,
                itemStyle: {
                  borderColor: '#3EC3E3',
                  borderWidth: 0.2,
                  areaColor: {
                    type: 'radial',
                    x: 0.5,
                    y: 0.5,
                    r: 0.8,
                    colorStops: [
                      {
                        offset: 0,
                        color: '#1867B5'
                      },
                      {
                        offset: 1,
                        color: '#1867B5'
                      }
                    ],
                    globalCoord: true
                  }
                }
              },
              series: [
                // city effect scatter
                {
                  type: 'effectScatter',
                  coordinateSystem: 'geo',
                  zlevel: 2,
                  rippleEffect: {
                    period: 15,
                    brushType: 'stroke',
                    scale: 5
                  },
                  label: {
                    show: true,
                    position: 'right',
                    offset: [5, 0],
                    formatter: '{b}',
                    color: '#fff',
                    fontSize: 15
                    // color: 'rgb(214,206,143)'
                  },
                  // map country effect scatter chart
                  tooltip: {
                    formatter: (_) => {
                      this.tooltipWater = _
                      return this.handleTooltipHtml(_)
                    }
                  },
                  emphasis: {
                    show: true,
                    label: {
                      fontSize: 18,
                      fontWeight: 'bold'
                    }
                  },
                  symbol: 'circle',
                  symbolSize: '10',
                  itemStyle: {
                    show: false,
                    color: '#0FD1D9'
                  },
                  data: cities
                },
                // dynamic line
                {
                  type: 'lines',
                  coordinateSystem: 'geo',
                  large: true,
                  zlevel: 1,
                  effect: {
                    show: true,
                    period: 11, //箭头指向速度，值越小速度越快
                    trailLength: 0.1, //特效尾迹长度[0,1]值越大，尾迹越长重
                    symbol: 'arrow', //箭头图标
                    symbolSize: 6 //图标大小
                  },
                  lineStyle: {
                    color: '#0FD1D9',
                    // 线条宽度
                    width: 0.2,
                    opacity: 1,
                    curveness: 0.25
                  },
                  label: {
                    show: false,
                    position: 'middle',
                    formatter: '{b}'
                  },
                  // line chart
                  tooltip: {
                    show: false
                  },
                  data: moveLines
                }
              ]
            }
            this.chart.setOption(option)
          }
        })
    },
    // button click handler:check all ranking info
    handleCheckBtn() {
      this.dialogVisible = true
    },
    // button click handler:switch to nation chart
    handleToNation(p) {
      this.$emit('toNation')
    },
    // template of tooltip
    handleTooltipHtml(data) {
      const d = data.data || {}
      const da = d.value && Array.isArray(d.value) && d.value[2]
      let rentingAll = 0
      let operationAll = 0
      for (const ky in da) {
        ky.includes('GensetInfoStatus_') && (rentingAll += da[ky])
        ky.includes('GensetStatus') && (operationAll += da[ky])
      }
      // TODO 当前仅显示中国各省分布情况
      return `
<div style="color: #fff; font-size: 1.1rem; padding: 8px;">
  <div style="padding: 5px 5px;font-size:1.2rem">
    机组分类（${rentingAll}）
    <span
      style="position: absolute; right: 10px; color: #0fd1d9;
       cursor: pointer;display:${d.name === '中国' ? '' : ''}"
      id="ms-to-nation-btn"
      data-country_name = "${d.name}"
    >
      ${d.name}>
    </span>
  </div>
  <div style="display: flex;">
    <div style="margin: 2px 10px; flex:1">待租赁 ${da.GensetInfoStatus_0Count}</div>
    <div style="margin: 2px 10px; flex:1">租赁中 ${da.GensetInfoStatus_1Count}</div>
    <div style="margin: 2px 10px; flex:1">施工 ${da.GensetInfoStatus_3Count}</div>
  </div>
  <div style="display: flex;">
    <div style="margin: 2px 10px; flex:1">检修 ${da.GensetInfoStatus_4Count}</div>
    <div style="margin: 2px 10px; flex:1">维修 ${da.GensetInfoStatus_2Count}</div>
    <div style="margin: 2px 10px; flex:1"></div>
  </div>
  <div style="padding: 5px 5px;font-size:1.2rem">租赁中机组运行状态（${operationAll}）</div>
  <div style="display: flex;">
    <div style="margin: 2px 10px; flex:1">正常 ${da.GensetStatusRun}</div>
    <div style="margin: 2px 10px; flex:1">故障 ${da.GensetStatusAlarm}</div>
    <div style="margin: 2px 10px; flex:1">风险 ${da.GensetStatusWarn}</div>
  </div>
  <div style="display: flex;">
    <div style="margin: 2px 10px; flex:1">停机 ${da.GensetStatusStop}</div>
    <div style="margin: 2px 10px; flex:1">离线 ${da.GensetStatusOffline}</div>
    <div style="margin: 2px 10px; flex:1"></div>
  </div>
</div>    

            `
    },
    // begin countdown
    handleCountdown() {
      this.autoplayTimer = setInterval((_) => {
        this.autoplayTime--
      }, 1000)
    },
    // autoplay methods
    handleAutoplayBegin() {
      this.autoplayFlag = true
      this.autoplayTime = this.autoplayTimeMax
      this.handleCountdown()
    },
    handleAutoplayStop() {
      this.autoplayFlag = false
      clearInterval(this.autoplayTimer)
    },
    // keyboard event
    handleKeydown(e) {
      const keycode = e.code
      if (keycode === 'Space') {
        this.$message.success(this.autoplayFlag ? '已暂停自动展播！' : '已开始自动展播！')
        this.autoplayFlag ? this.handleAutoplayStop() : this.handleAutoplayBegin()
      }
    }
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {
    document.addEventListener('keydown', this.handleKeydown)
    this.getMapData()
    this.handleCountdown()
  },
  beforeUpdated() {},
  updated() {},
  beforeDestroy() {
    this.chart.clear()
    document.removeEventListener('keydown', this.handleKeydown)
  },
  destroyed() {}
}
</script>
<template>
  <div class="e-charts-map-rotation-container">
    <div class="e-charts-map-rotation"></div>
    <div class="legend-component" v-if="rankingVisible">
      <div class="title">
        <span>全球智能装备数量Top10</span>
        <el-button class="check-all-btn" type="text" @click="handleCheckBtn"> > </el-button>
      </div>
      <ranking :chartDataOriginal="chartDataOriginal" :top="10"></ranking>
    </div>
    <div class="autoplay-bottom" v-if="autoplayFlag">
      <svg-icon
        class="icon"
        slot="prefix"
        icon-class="play"
        title="点击暂停播放"
        @click="handleAutoplayStop"
      />
      <span class="number">{{ autoplayTime }}</span
      >S后将自动展示智能装备中国分布图
      <span class="link" title="点击立即跳转" @click="handleToNation"> ></span>
    </div>
    <div class="autoplay-bottom" v-else>
      <svg-icon
        class="icon"
        slot="prefix"
        icon-class="pause"
        title="点击开始自动展播"
        @click="handleAutoplayBegin"
      ></svg-icon>
      已停止自动展播
    </div>
    <el-dialog
      width="30%"
      :modal="true"
      :modal-append-to-body="false"
      title="数据详情"
      class="legend-dialog"
      :visible.sync="dialogVisible"
    >
      <ranking :chartDataOriginal="chartDataOriginal" :all="true"> </ranking>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<style lang='less' scoped>
.e-charts-map-rotation-container {
  width: 100%;
  height: 100%;
  position: relative;
  .e-charts-map-rotation {
    width: percentage(1);
    height: percentage(1);
  }
  .legend-component {
    left: 10px;
    bottom: 0;
    display: flex;
    color: white;
    font-size: 1.2rem;
    position: absolute;
    flex-direction: column;
    width: percentage(0.3);
    height: percentage(0.6);
    .title {
      font-size: 1.2rem;
      margin: 0 0 0 10px;
    }
    /deep/ .check-all-btn {
      span {
        // font-weight: 600;
        font-size: 1.5rem;
        color: #0fd1d9;
      }
    }
  }
  .autoplay-bottom {
    bottom: 0;
    right: 10px;
    display: flex;
    font-size: 1.3rem;
    position: absolute;
    align-items: center;
    .icon {
      cursor: pointer;
      font-size: 2.2rem;
    }
    .number {
      margin: 0 5px;
      color: #24b690;
      font-size: 1.8rem;
    }
    .link {
      margin: 0 5px;
      cursor: pointer;
      color: #0fd1d9;
    }
  }
  /deep/.legend-dialog {
    .el-dialog {
      z-index: 9999;
    }
    .el-dialog__body {
      height: 60vh;
      overflow: scroll;
    }
  }
  .my-tooltip {
    background: black;
  }
}
</style>


